.radioButton {
    position: relative;
    margin: 10px;
}

.radioButton input {
    position: absolute;
    padding: 0;
    height: 0;
    width: 0;
}

.radioButton .fa-circle-check {
    position: absolute;
    cursor: pointer;
    top: 12px;
    left: 0;
    color: #0d6efd;
    display: none;
}

.radioButton .fa-circle {
    position: absolute;
    cursor: pointer;
    color: #DDD;
    top: 12px;
    left: 0;
}
.radioButton label p{
    cursor: pointer;
    margin-left: 25px;
}

.radioButton input:checked~label .fa-circle {
    display: none;
}

.radioButton input:checked~label .fa-circle-check {
    display: block;
}