a{
    text-decoration: none;
    color: #FFF;
    border-bottom: 2px solid transparent;
    padding-bottom: 6px;
    font-weight: 500;
  }
  a.active{
    border-bottom: 2px solid #FFF;
    font-weight: 500;
  }
  #root{
    height: 100vh;
  }
  .quiz{
    min-height: 340px;
    width: 500px;
    border-radius: 5px;
    margin-top: 20px;
    background-color: #FFF;
  }
  .score{
    min-height: 300px;
    width: 500px;
    border-radius: 5px;
    margin-top: 10px;
    background-color: #FFF;
  }
  .score .options{
    margin: 25px 5px;
  }
  .fa-circle-check{
    color: #55F;
  }
  .fa-circle{
    color:#DDD;
  }
  .options{
    margin: 7px 5px;
  }
  .Votes{
    font-size: 12px;
    white-space: pre-wrap;
  }

  .radio{
    height: 30px;
    width: 30px;
    background-color: red !important;
  }
  .Survey{
    width: 300px;
  }
  @media only screen and (max-width: 740px) {
    .Survey{
      width: 400px;
    }
  }